<template>
    <!--Модуль блока "Действия"-->
    <div class="col-12 pr-0">
       
        <div class="row mb-3">
            <div class="col-12 pr-0">
                <div class="row">
                    <div class="col-2 pl-0 pr-0 collapse-list">
                        <button type="button" class="btn btn-success btn-circle"><i class="far fa-comments"></i> </button>
                    </div>
                    <div class="col-10 pl-0">
                        <div class="font-tab-mid">
                            <i class="fas fa-angle-left"></i>
                            Оставить комментарий
                        </div>
                    </div>
                </div>
            </div>
        </div>
        <div class="row pl-0 pr-0 mb-3">
            <div class="col-12 pr-0">
                <div class="row">
                    <div class="col-2 pl-0 pr-0 collapse-list">
                        <button type="button" class="btn btn-warning btn-circle"><i class="fas fa-paper-plane"></i> </button>
                    </div>
                    <div class="pl-0 col-10">
                        <div class="font-tab-mid">
                            <i class="fas fa-angle-left"></i>
                            Отправить на рассмотрение
                        </div>
                    </div>
                </div>
            </div>
        </div>
        <div class="row mb-3">
            <div class="col-12 pr-0">
                <div class="row">
                    <div class="col-2 pl-0 pr-0 collapse-list">
                        <button type="button" class="btn btn-danger btn-circle"><i class="mdi mdi-close"></i> </button>
                    </div>
                    <div class="pl-0 col-10">
                        <div class=" font-tab-mid">
                            <i class="fas fa-angle-left"></i>
                            Аннулировать
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
  export default {
    name: 'actions-document'
  }
</script>

<style scoped>
    .font-tab-mid{
        margin-top: 15px;
        font-size: 1rem;
    }
    hr{
        border: none;
        background-color: #2a3035;
        color: #2a3035;
        height: 1px;
    }
    .collapse-list{
        color: #455a64;
        list-style-type: none;
    }
    .toggle-btn ul li{
        list-style-type: none;
        margin-left: -30px;
    }
    .toggle-btn-line ul li{
        display: inline;
        list-style-type: none;
    }
</style>



